import { Component, h, Element, Prop } from '@stencil/core';

declare const UIkit: any;

@Component({
	tag: 'ks-order-login-prompt'
})
export class OrderLoginPrompt {
    @Element() root: HTMLElement;
    @Prop() message: string = "";
    @Prop() heading: string = "";
    @Prop() toCart: string = "";
    @Prop() toConfirmation: string = "";

    async componentDidLoad() {

        const element = document.querySelector("#ks-order-login-prompt");
        UIkit.modal(element).show();
    }

    ToCart() {
        window.location.href = 'koszyk.html';
    }

    ToConfirmation() {
        window.location.href = 'zamowienie-potwierdzenie.html';
    }

	render() {
		return(
            <div id="ks-order-login-prompt" class="uk-modal-full" uk-modal>
                <div class="uk-modal-dialog">
                    <div class="uk-background-cover uk-flex uk-flex-between uk-flex-column" uk-height-viewport>

                        <div class="uk-padding-small uk-flex-auto uk-flex uk-flex-center uk-flex-middle uk-flex-column">
                            <div class="uk-text-center">
                                <span class="ks-text-decorated uk-h1 uk-text-bold">{this.heading}</span><br/>
                                <p>{this.message}</p>
                            </div>
                            <div class="uk-margin-top">
                                <button onClick={()=>this.ToCart()} class="uk-button uk-button-secondary" style={{padding: "5px 15px"}}>
                                    {this.toCart}
                                </button>

                                <button onClick={()=>this.ToConfirmation()} class="uk-button uk-button-danger" style={{padding: "5px 15px"}}>
                                    {this.toConfirmation}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}
